import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {
  q = {
    username: '',
    email: '',
    gender: '',
  };

  form: FormGroup;
  brands:any;
  models:any;
  chargerTypes:any;
  localStorage : any;
  state$:any;

  constructor(private fb: FormBuilder, private masterService : MasterService,
    private vehicleService : VehicleService,private snackBar: MatSnackBar,
    private router : Router,private activatedRoute : ActivatedRoute) {
     
    
  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = localStorage.getItem('userDetails');

    this.activatedRoute.queryParams.subscribe(params => {
      const userId = params['userId'];
    });

    this.state$ = this.activatedRoute.paramMap
      .pipe(map(() => window.history.state));

    this.form = this.fb.group({
      brand: ['', [Validators.required]],
      model: ['', [Validators.required]],
      chargerType : ['', [Validators.required]],
      registration: ['', [Validators.required,
        Validators.minLength(8),Validators.maxLength(11),
        Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      yearOfManufacture: ['', [Validators.required]],
      engineNumber: ['', [Validators.required]],
      chassisNumber: ['', [Validators.required]],
      VINnumber: ['', [Validators.required]],
      // vehicle: ['', [Validators.required]],
      // manufacturer: ['', [Validators.required]],
      
    });

    this.getBrands();
  }

  getBrands(){
    this.masterService.getBrands().subscribe(
      (data:any)=>{
        this.brands = data;
      },
      (err)=>{
        ////
      })
  }
  getModelsByBrand(id){
    this.masterService.getModelsByBrand(id).subscribe(
      (data:any)=>{
        this.models = data;
      },
      (err)=>{
        ////
      })
  }
  getConnectorTypeByModel(id){
    this.masterService.getConnectorTypeByModel(id).subscribe(
      (data:any)=>{
        this.chargerTypes = data;
      },
      (err)=>{
        ////
      })
  }

  onBrandChange(event){
    this.getModelsByBrand(event.value);
    this.form.get('model').setValue("");
  }
  onModelChange(event){
    this.getConnectorTypeByModel(event.value);
  }
  onChargerTypeChange(event){
    ////
    // this.getChargerTypeByModel(event.value);
  }


  onSubmit(){
    let formValues = this.form.value;
    ////

    let params = {
        brand_id : formValues.brand,
        model_id : formValues.model,
        charger_type : formValues.chargerType,
        registration_no : formValues.registration,
        year_of_manufacture : formValues.yearOfManufacture,
        engine_no : formValues.engineNumber,
        chassis_no : formValues.chassisNumber,
        vin_no : formValues.VINnumber,
        status : 'Y',
        created_by : this.localStorage.userId
    }
    this.vehicleService.register(params).subscribe(
      (data:any )=>{
        ////
        // this.snackBar.open('Vehicle Registered!', 'Dismiss', { duration: 5000 });
        this.router.navigate(['/registration/vehicle/list']);

      },
      (err)=>{
        ////
        this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
      }
    )

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
}


