export class ListModrate {
  vm_name: string;
  ct_name:string;
  vType_name:string;
  created_date:string;
  dateConverted:string;
}

/**
 * @title Table with sorting
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class ListComponent implements OnInit {
  filterEntity: ListModrate;
  filterType: MatTableFilter;
  displayedColumns: string[] = [ 'vm_name','ct_name','vType_name','created_date','action'];
  head = [['VM_Name', 'CT_Name','VType_Name','Created_Date']];
  dataSource : MatTableDataSource<ListModrate>;
  vehicles : ListModrate[];
  localStorage : any;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  vehicleSearch = false;
  connTypeSearch = false;
  vehicleTypeSearch = false;
  modrateSearch = false;
  exportedData: any[];
  constructor(private vehicleService : VehicleService,private router : Router,private datePipe:DatePipe,
    public dialog : MatDialog,private snackBar: MatSnackBar){}

  ngOnInit() {
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.getVModel_CTypeMapping();
    this.filterEntity = new ListModrate();
           this.filterType = MatTableFilter.ANYWHERE;
  }

  getVModel_CTypeMapping(){
    ////
    this.vehicleService.getVModel_CTypeMapping().subscribe(
      (data:any)=>{
        this.vehicles = data;
        for(var i =0; i<this.vehicles.length;i++){
          this.vehicles[i].dateConverted = this.datePipe.transform(this.vehicles[i].created_date, 'dd/MM/yyyy');
        }
        this.dataSource = new MatTableDataSource(this.vehicles);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err)=>{
        ////
      }
    )
  }

  edit(item){
    ////
    this.router.navigate(['/registration/vehicle/edit',{ state: {data : JSON.stringify(item)} }]);
  }

  publish(data){
    if(data.published_with_modification==0){
      this.vehicleService.publishVModel_CTYpeWithoutModify(data).subscribe(
        (data:any)=>{
          this.snackBar.open(MessageConstant.publishedMessage, 'Dismiss', { duration: 3000 });
          this.getVModel_CTypeMapping();
  
        },(err)=>{
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
        }
      )

    }else{
      this.vehicleService.publishVModel_CTYpe(data).subscribe(
        (data:any)=>{
          //
          this.snackBar.open(MessageConstant.publishedMessage, 'Dismiss', { duration: 3000 });
          this.getVModel_CTypeMapping();
  
        },(err)=>{
          //
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
        }
      )
    }
  }

  openDialog(data): void {

    //
    let params = {
      action : 'PUBLISH',
      data : data
    }

    const dialogRef = this.dialog.open(ActionModalComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      //
      // this.email = result;
      this.publish(result);
    });
  }
  viewDialog(data): void {

    let params = {
      action : 'PUBLISH',
      data : data
    }

    const dialogRef = this.dialog.open(ViewModalComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      //
      this.publish(result);
    });
  }
  delete(id){

    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
  });

  //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

  this.confirmDialogRef.afterClosed().subscribe(result => {
      if ( result )
      {
        this.vehicleService.deleteModrateVehicle(id,this.localStorage.userId).subscribe(
          (data:any)=>{
            this.getVModel_CTypeMapping();
            this.snackBar.open(MessageConstant.deleteMessage,'Dismiss',{duration:2000});
          },(err)=>{
          
          }
        )
      }
      this.confirmDialogRef = null;
  });
   
  }
  changeVisibility(visibleOption,ev){
    // visibleOption = !visibleOption;
    //nameSearch = false;
      //descSearch = false;
      ev.stopPropagation();
    if(visibleOption == 'vehicleSearch'){
      this.vehicleSearch = !this.vehicleSearch;
      this.filterEntity.vm_name = '';
    }
    
    if(visibleOption == 'connTypeSearch'){
      this.connTypeSearch = !this.connTypeSearch;
      this.filterEntity.ct_name = '';
    }
    if(visibleOption == 'vehicleTypeSearch'){
      this.vehicleTypeSearch = !this.vehicleTypeSearch;
      this.filterEntity.vType_name = '';
    }
    if(visibleOption == 'modrateSearch'){
      this.modrateSearch = !this.modrateSearch;
      this.filterEntity.dateConverted = '';
    } 
   }
   tableExport()
   {
    
     this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
       var chargerModel = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {'Vehicle Model ':chargerModel.vm_name,'Connector Type':chargerModel.ct_name,'Vehicle Type':chargerModel.vType_name,
            'Publish Date':chargerModel.dateConverted};
      this.exportedData.push(data);
    }


     const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
       const workBook: XLSX.WorkBook = XLSX.utils.book_new();
     
       XLSX.utils.book_append_sheet(workBook, workSheet, 'Modrate list');
       XLSX.writeFile(workBook, 'Modrate List.xlsx');
   }

   pdfExport(){
    //this.preloader.open();
    var doc = new jsPDF({ // set needed dimensions for any elemen
      
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chrger = this.dataSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[chrger.vm_name,chrger.ct_name,chrger.vType_name,chrger.dateConverted] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
   
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
        
      },
     
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Modrate List Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
  
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    //this.preloader.close();
    // below line for Download PDF document  
    doc.save('Modrate List report.pdf');
   
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right")

    return doc;
  
  }
}
