import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OnBoardComponent } from './on-board/on-board.component';
import { TABS_DEMO_ROUTES } from './on-board/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    // { path : 'onBoard/create', component : OnBoardComponent, children: TABS_DEMO_ROUTES},
    { path : 'onBoard', component : OnBoardComponent,canActivate: [AuthGuard], children: TABS_DEMO_ROUTES},
    { path : '', redirectTo : 'onBoard',pathMatch:'full'}
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class VehicleOnBoardRoutingModule{}