

import { Component, OnInit,Inject } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
interface DialogData {
  email: string;
}

@Component({
  selector: 'app-action-modal',
  templateUrl: './action-modal.component.html',
  styleUrls: ['./action-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ActionModalComponent implements OnInit {

  params : any;
  form: FormGroup;
  brands:any;
  models:any;
  connectorTypes:any;
  vehicleTypes:any;
  localStorage : any;
  isEnabled = 0;

  filteredOptionsBrand: Observable<any[]>;
  filteredOptionsModel: Observable<any[]>;
  filteredOptionsVehicleType: Observable<any[]>;
  constructor(private fb: FormBuilder, private masterService : MasterService,
    public dialogRef: MatDialogRef<ActionModalComponent>, @Inject(MAT_DIALOG_DATA) public data: DialogData) {
     
    
  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){
    

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.params = this.data;
    // 

    this.form = this.fb.group({
      brand: ['', [autocompleteObjectValidator(),Validators.required]],
      model: ['', [autocompleteObjectValidator(),Validators.required]],
      connectorType : ['', [Validators.required]],
      vehicleType : ['', [autocompleteObjectValidator(),Validators.required]],
      isModified : [''] ,     
      isConfirmed : ['']      
    });

    this.getBrands();
    this.getVehicleTypes();
    this.form.disable();
    this.filterOption();
    this.assignValues(this.data);
  }

  getBrands(){
    this.masterService.getBrands().subscribe(
      (data:any)=>{
        this.brands = data;
        const toSelect = this.brands.find(c => c.id == this.params.data.brand_id);
        this.form.get('brand').setValue(toSelect);
        this.filteredOptionsBrand = this.form.controls.brand.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterBrands(name) : this.brands)
        );
        this.onBrandChange(toSelect.id);

      },
      (err)=>{
        ////
      })
  }
  getVehicleTypes(){
    this.masterService.getVehicleTypes().subscribe(
      (data:any)=>{
        this.vehicleTypes = data;

        const toSelect = this.vehicleTypes.find(c => c.id == this.params.data.vType_id);
        
        this.form.get('vehicleType').setValue(toSelect);
        this.filteredOptionsVehicleType = this.form.controls.vehicleType.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterVehicleType(name) : this.vehicleTypes)
        );
      },
      (err)=>{
        ////
      })
  }
  getModelsByBrand(id){
    this.masterService.getModelsByBrand(id).subscribe(
      (data:any)=>{
        this.models = data;
        
        const toSelect = this.models.find(c => c.id == this.params.data.vm_id);
        this.form.get('model').setValue(toSelect);
        this.filteredOptionsModel = this.form.controls.model.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterModels(name) : this.models)
        );
        this.onModelChange(toSelect.id);
      },
      (err)=>{
        ////
      })
  }

  getAllCTypesExcludingOtherAlreadyMapped(vm_id,ct_id){
    
    this.masterService.getAllCTypesExcludingOtherAlreadyMapped(vm_id,ct_id).subscribe(
      (data:any)=>{
        
        this.connectorTypes = data;
        const toSelect = this.connectorTypes.find(c => c.id == this.params.data.ct_id);
        this.form.get('connectorType').setValue(toSelect.id);
      },
      (err)=>{
        
      })
    
  }

  getConnectorTypes(vm_id){
    this.connectorTypes=null;
    this.masterService.connectorTypesExcludingVModelId(vm_id).subscribe(
      (data:any)=>{
        
        this.connectorTypes = data;
        const toSelect = this.connectorTypes.find(c => c.id == this.params.data.ct_id);
        this.form.get('connectorType').setValue(toSelect.id);
      },
      (err)=>{
        
      })
  }
  closeActionPanel(){
    this.dialogRef.close();
  }


  onBrandChange(id){
    this.getModelsByBrand(id);
    this.form.get('model').setValue("");
  }
  onModelChange(id){
    
    if(id != this.params.data.vm_id){
      this.getConnectorTypes(id);
    }else{
      this.getAllCTypesExcludingOtherAlreadyMapped(id,this.params.data.ct_id);
    }
  }

  onNoClick(): void {
    //
    this.dialogRef.close();
  }

  onYesClick(){
    //
    //if(this.form.valid){
      let formValues = this.form.value;
    

      let params ={}
      if(this.isEnabled){
       params = {
            id : this.params.data.id,
            ct_id: formValues.connectorType,
            vm_id: formValues.model.id,
            vType_id : formValues.vehicleType.id,
            status : 'P',
            published_with_modification : this.isEnabled,
            published_by : this.localStorage.userId
        }
      }else{
        params = {
          id : this.params.data.id,
          status : 'P',
          published_with_modification : this.isEnabled,
          published_by : this.localStorage.userId
        }
      }
  
      this.dialogRef.close(params);
    //}
   return;
  }


  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  changeEdit(event){
    //
    if(event.checked){
      this.form.enable();
      this.isEnabled=1;
    }else{
      this.form.disable();
      this.isEnabled=0;
    }
  }


  assignValues(data){
    //

  }
  filterOption(){
    this.filteredOptionsBrand = this.form.controls.brand.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterBrands(name) : this.brands)
        );
        this.filteredOptionsModel = this.form.controls.model.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterModels(name) : this.models)
        );
        this.filteredOptionsVehicleType = this.form.controls.vehicleType.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterVehicleType(name) : this.vehicleTypes)
        );
  }
  displayBrand(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayModel(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayVehicleType(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterBrands(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.brands.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.models.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterVehicleType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.vehicleTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }

}



