import { Routes } from '@angular/router';

import { CreateComponent } from 'app/main/asset/vehicle/vehicle-onBoard/create/create.component';
import { EditComponent } from 'app/main/asset/vehicle/vehicle-onBoard/edit/edit.component';
import { ListComponent } from 'app/main/asset/vehicle/vehicle-onBoard/list/list.component';
import { PublishComponent } from 'app/main/asset/vehicle/vehicle-onBoard/publish/publish.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';

export const TABS_DEMO_ROUTES: Routes = [
  { path : 'create', component : CreateComponent, canActivate:[AuthGuard] },
  { path : 'edit/:id', component : EditComponent , canActivate:[AuthGuard] },
  { path : 'list', component : ListComponent , canActivate : [AuthGuard] },
  { path : 'publish', component : PublishComponent , canActivate:[AuthGuard]},
  { path: '', redirectTo: 'publish', pathMatch: 'full' },
];
