import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { VehicleOnBoardRoutingModule } from './vehicle-onBoard-routing.module';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';
import { PublishComponent } from './publish/publish.component';
import { OnBoardComponent } from './on-board/on-board.component';
import { ActionModalComponent } from './action-modal/action-modal.component';
// import { ChargingStationRoutingModule } from './charging-station-routing.module';
import { FuseSharedModule  } from '@fuse/shared.module';
import { ViewModalComponent } from './view-modal/view-modal.component';
const COMPONENTS =[CreateComponent,ListComponent,EditComponent];
const COMPONENTS_DYNAMIC =[ActionModalComponent,ViewModalComponent];

@NgModule({
    imports:[SharedModule, VehicleOnBoardRoutingModule,FuseSharedModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC, PublishComponent, OnBoardComponent, ActionModalComponent],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class VehicleOnBoardModule{}