import {Component, OnInit, ViewChild} from '@angular/core';
import {MatSort} from '@angular/material/sort';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { Router } from '@angular/router';

import {MatDialog} from '@angular/material/dialog';
import {ActionModalComponent} from '../action-modal/action-modal.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableFilter } from 'mat-table-filter';
import {ViewModalComponent} from '../view-modal/view-modal.component';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { DatePipe } from '@angular/common';
interface DialogData {
  email: string;
}
export class Configuration {
  vm_name:string;
  vType_name:string;
  ct_name:string;
  created_date:string;
}
export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}
export class ListPublish {
  vm_name: string;
  ct_name:string;
  vType_name:string;
  created_date:string;
  dateConverted:string;
 
}
@Component({
  selector: 'app-publish',
  templateUrl: './publish.component.html',
  styleUrls: ['./publish.component.scss'],
  providers:[DatePipe]
})
export class PublishComponent implements OnInit {

  filterEntity: ListPublish;
  filterType: MatTableFilter;
  displayedColumns: string[] = [ 'vm_name','ct_name','vType_name','created_date','action'];
  head = [['VM_Name', 'CT_Name','VType_Name','Created_Date']];
  dataSource : MatTableDataSource<ListPublish>;
  vehicles : ListPublish[];

  email: string;

  @ViewChild(MatSort, {static: true}) sort: MatSort;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  vehicleSearch = false;
  connTypeSearch = false;
  vehicleTypeSearch = false;
  publishSearch = false;
  exportedData: any[];
  localStorage : any;
  constructor(private vehicleService : VehicleService,private router : Router,private datePipe:DatePipe,
    public dialog : MatDialog, private snackBar : MatSnackBar){}

  ngOnInit() {
    this.localStorage = localStorage.getItem('userDetails');
    this.getPublishedVModel();
    this.filterEntity = new ListPublish();
           this.filterType = MatTableFilter.ANYWHERE;
  }

  getPublishedVModel(){
    this.vehicleService.getPublishedVModel().subscribe(
      (data:any)=>{

        this.vehicles = data;
        for(var i =0; i<this.vehicles.length;i++){
          this.vehicles[i].dateConverted = this.datePipe.transform(this.vehicles[i].created_date, 'dd/MM/yyyy');
        }
        this.dataSource = new MatTableDataSource(this.vehicles);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        
      },
      (err)=>{
      }
    )
  }
  
  moderate(data){

    if(data.published_with_modification==0){
      this.vehicleService.moderateVModel_CTYpeWithoutModify(data).subscribe(
        (data:any)=>{
          this.snackBar.open('Moderated successfully', 'Dismiss', { duration: 3000 });
          this.getPublishedVModel();
  
        },(err)=>{
          this.snackBar.open('Please try again', 'Dismiss', { duration: 3000 });
        }
      )

    }else{
      this.vehicleService.moderateVModel_CTYpe(data).subscribe(
        (data:any)=>{
          //
          this.snackBar.open('Moderated successfully', 'Dismiss', { duration: 3000 });
          this.getPublishedVModel();
  
        },(err)=>{
          //
          this.snackBar.open('Please try again', 'Dismiss', { duration: 3000 });
        }
      )
    }
    
  }

  openDialog(data): void {

    let params = {
      action : 'MODERATE',
      data : data
    }

    const dialogRef = this.dialog.open(ActionModalComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      //
      this.moderate(result);
    });
  }
  viewDialog(data): void {

    let params = {
      action : 'MODERATE',
      data : data
    }

    const dialogRef = this.dialog.open(ViewModalComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      //
      this.moderate(result);
    });
  }
  changeVisibility(visibleOption,ev){
    // visibleOption = !visibleOption;
    //nameSearch = false;
      //descSearch = false;
      ev.stopPropagation();
    if(visibleOption == 'vehicleSearch'){
      this.vehicleSearch = !this.vehicleSearch;
      this.filterEntity.vm_name = '';
    }
    
    if(visibleOption == 'connTypeSearch'){
      this.connTypeSearch = !this.connTypeSearch;
      this.filterEntity.ct_name = '';
    }
    if(visibleOption == 'vehicleTypeSearch'){
      this.vehicleTypeSearch = !this.vehicleTypeSearch;
      this.filterEntity.vType_name = '';
    }
    if(visibleOption == 'publishSearch'){
      this.publishSearch = !this.publishSearch;
      this.filterEntity.dateConverted = '';
    }
   }
   tableExport()
   {
    
     this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
       var chargerModel = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {'Vehicle Model ':chargerModel.vm_name,'Connector Type':chargerModel.ct_name,'Vehicle Type':chargerModel.vType_name,
            'Publish Date':chargerModel.dateConverted};
      this.exportedData.push(data);
    }


     const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
       const workBook: XLSX.WorkBook = XLSX.utils.book_new();
     
       XLSX.utils.book_append_sheet(workBook, workSheet, 'Publish list');
       XLSX.writeFile(workBook, 'Publish List.xlsx');
      
      
   
   }

   pdfExport(){
    //this.preloader.open();
    var doc = new jsPDF({ // set needed dimensions for any elemen
      
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chrger = this.dataSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[chrger.vm_name,chrger.ct_name,chrger.vType_name,chrger.dateConverted] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
   
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
        
      },
     
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Publish List Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
  
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    //this.preloader.close();
    // below line for Download PDF document  
    doc.save('Publish_List_report.pdf');
   
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right")
    
    
  
    return doc;
  
  }
  
}
